import React, { useState } from 'react';
import './App.css';
import Header from '../Header/Header';
import Form from '../Form/Form';
import DateInfo from '../DateInfo/DateInfo';

const App = () => {

  const [dates, setDates] = useState([]);
  const [deletePopUp, setdeletePopUp] = useState(false);

  const dateinfo = date => {
    const newDate = {
      id: Date.now(),
      namePet: date.namePetRef,
      nameOwner: date.nameOwnerRef,
      date: date.dateRef,
      symptom: date.symptomRef
    }
    setDates([...dates, newDate]);
  }

  const deleteCardInfo = id => {
    setDates(dates.filter(date => date.id !== id))
    setdeletePopUp(true);
    setTimeout(() => { setdeletePopUp(false) }, 3000);
  }

  return (
    <div className="App">
      <Header />
      <div className='main'>
        <Form dateinfo={dateinfo} deletePopUp={deletePopUp} />
        <DateInfo dates={dates} deleteCardInfo={deleteCardInfo} />
      </div>
    </div>
  );
}

export default App;
