import React from 'react'
import './DateInfo.css';

const DateInfo = (props) => {
    let dates = props.dates;

    const deleteBtn = id => {
        props.deleteCardInfo(id);
    }

    return (
        <div className='card-container'>
            {dates.map(date => (
                <div className='card' key={date.id}>
                    <div className='card-info'>
                        <div className='card-ico pet'></div>
                        <div className='info'>{date.namePet}</div>
                    </div>
                    <div className='card-info'>
                        <div className='card-ico user'></div>
                        <div className='info'>{date.nameOwner}</div>
                    </div>
                    <div className='card-info'>
                        <div className='card-ico date'></div>
                        <div className='info'>{date.date}</div>
                    </div>
                    <div className='card-info'>
                        <div className='card-ico symp'></div>
                        <div className='info'>{date.symptom}</div>
                    </div>
                    <div className='card-info'>
                        <button onClick={() => deleteBtn(date.id)} className='btn-delete' >ELIMINAR</button>
                    </div>
                </div>
            ))}
        </div>
    )
}

export default DateInfo;