import React, { useState } from 'react';
import './Form.css';

const Form = props => {

    const [errorPopUp, setErrorPopUp] = useState(false);
    const [successPopUp, setSuccessPopUp] = useState(false);

    let namePetRef = React.createRef();
    let nameOwnerRef = React.createRef();
    let dateRef = React.createRef();
    let symptomRef = React.createRef();

    const addDate = e => {
        e.preventDefault();
        const dates = {
            namePetRef: namePetRef.current.value,
            nameOwnerRef: nameOwnerRef.current.value,
            dateRef: dateRef.current.value,
            symptomRef: symptomRef.current.value
        }
        if (dates.namePetRef && dates.nameOwnerRef && dates.dateRef && dates.symptomRef) {
            props.dateinfo(dates);
            e.currentTarget.reset();
            setErrorPopUp(false);
            setSuccessPopUp(true);
            setTimeout(() => { setSuccessPopUp(false) }, 3000);
        } else {
            setErrorPopUp(true);
            setTimeout(() => { setErrorPopUp(false); }, 3000);
        }

    }

    return (
        <React.Fragment>
            <form className='form' onSubmit={addDate}>
                <div className='form-header'><h3>Ingreso de citas</h3></div>
                <div className='form-group'>
                    <div className='form-input'>
                        <label>Nombre Mascota</label>
                        <input ref={namePetRef} type="text" />
                    </div>
                    <div className='form-input'>
                        <label>Nombre Due√±o</label>
                        <input ref={nameOwnerRef} type="text" />
                    </div>
                    <div className='form-input'>
                        <label>Fecha</label>
                        <input ref={dateRef} type="date" />
                    </div>
                    <div className='form-input'>
                        <label>Sintomas</label>
                        <textarea ref={symptomRef}></textarea>
                    </div>
                    <div className='form-input'>
                        <button className='add-btn' type="submit">AGREGAR</button>
                    </div>
                    <div className='form-input'>
                        {errorPopUp ? <div className='PopUp error'><h5>Todos los campos son obligatorios</h5></div> : ''}
                        {successPopUp ? <div className='PopUp success'><h5>Cita agregada exitosamente</h5></div> : ''}
                        {props.deletePopUp ? <div className='PopUp delete'><h5>Cita eliminada exitosamente</h5></div> : ''}
                    </div>
                </div>
            </form>
        </React.Fragment>
    )
}

export default Form
